@import "../../styles/scss/variables";

.textareaWrapper {
  margin-top: 30px;
  position: relative;

  label {
    background: $mainBgcColor;
    font-size: .94rem;
    left: 15px;
    padding: 0 10px;
    pointer-events: none;
    position: absolute;
    top: -13px;
    transition: all .4s ease-out;
    display: inline-block;
    font-weight: 400;
    line-height: 1.8rem;
  }

  textarea {
    border: 1px solid $borderColor;
    font-size: 1rem;
    padding: 10px 15px;
    vertical-align: middle;
    background: transparent;
    line-height: inherit;
    color: $textColor;
    outline: none;
    resize: vertical;

  }
}

